.loader-boxed{
  animation: anim-loader 1s linear infinite;
  .loader-img{padding-top: 60%;background-color: #ccc}
  .loader-img-small{padding-top: 50%;background-color: #ccc}
  .loader-title{
    height: 35px;background-color: #ccc;margin-bottom: 25px;width: 100%;
  }
  .loader-text{height: 20px;margin-bottom: 15px;background-color: #ccc;width: 100%;}
  .loader-user{
    padding-left: 100px;position: relative;height: 100px;
    .img{width: 80px;height: 80px;border-radius: 50%;background-color: #ccc;position: absolute;left: 0;}
  }
  .loader-item{
    padding: 10px;
    .f-shadow{box-shadow: 0 0 3px rgba(0,0,0,.4)}
  }
}
@keyframes anim-loader{
  0%{opacity: 0.4}
  50%{opacity:1}
  100%{opacity:0.6}
}
