.slide-left{
  width: 100%;
  animation: slide-left .5s linear;
}
.slide-right{
  width: 100%;
  animation: slide-right .5s linear;
}
.img-icon-center{
  transform: rotate(180deg);-webkit-transform: rotate(180deg);-moz-transform: rotate(180deg);-o-transform: rotate(180deg);
  height: 50px; position: absolute;z-index: 10;left: 0;top: 0;right: 0;bottom: 0;cursor: pointer;padding: 10px;background-color: $primary-turqoise; border-radius: 10px;color: $white;margin: auto;
}
.img-icon-center:hover{
  color: $white;
  background-color: $red;
}
.user-info{
  .user-title{
    line-height: 1.4em;color: #565353;
    width: 200px;border-radius: 10px;position: absolute; top: 0; left: 20px;
    .header{
      font-size: 1.1em;font-weight: 600; margin-bottom: 10px;text-align: left;    border-bottom: 1px solid #ababab;
      padding-bottom: 10px;
    }
    .detile{
       margin-bottom: 15px;text-align: left;
    }
  }
  .user-img-box{width: 200px;height: 200px;border-radius: 10px;position: absolute; top: 0; left: 20px;}

  .user-input{position: relative;
    width: 100%;
    padding: 10px 20px;
    padding-left: 250px;
  }
}

.card-info{
  // .card-img{
  //   padding-top:55%;position: relative;
  //   img{
  //       width: 100%;height: 100%;border-radius: 10px;position: absolute; top: 0; left: 0;object-fit: contain;background-color: #ccc;
  //   }
  // }
  .card-input{position: relative;
    width: 100%;
    padding: 15px 20px;
    border-bottom: 2px solid #ccc;
    .text-info{
      padding: 10px 15px; line-height: 1.3em;
    }
  }
}


@media(max-width:768px){
  .user-img-box,.user-title{
    position: relative !important; display: inline-block;margin-bottom: 20px;left: 0 !important;
  }
  .user-title{width: 100% !important;}
  .user-img-container,.card-img-box{width: 100%;text-align: center;}
  .user-input{padding-left: 10px !important;}
}

@keyframes slide-left{
  0%{transform: translateX(-50%);opacity: 0;}
  100%{transform: translateX(0%);opacity: 1}
}
@keyframes slide-right{
  0%{transform: translateX(50%);opacity: 0}
  100%{transform: translateX(0%);opacity: 1}
}
