.fox-project{
  .fox-project-banner{
    animation: project-banner .4s linear;
    position: relative;
    padding-top: 60%;overflow: hidden;
    img{width: 100%;position: absolute;top: 0;left: 0;height: 100%;object-fit: cover;}
  }
  .fox-detile{
    animation: project-description .8s linear;
    padding: 0 20px; padding-right: 0;
    .fox-project-title{
      font-size: 2em;
      color: #041D57 !important;
      margin-bottom: 5px;
      line-height: 1.2em;
    }
    .fox-project-classter{
      display: inline-block;
      padding: 5px 10px;
      border: 2px solid $red;
      margin-bottom: 15px;
      border-radius: 16px;
      font-size: 13px;
    }
    .fox-project-sub-title{
      font-size: 1.1em;
      color: #615F5F;
      margin-bottom: 20px;
      line-height: 1.4em;
    }
    .fox-project-sub-title-weight{
      font-size: 1.6em;
      color: $red;
      margin-bottom: 20px;
      line-height: 1.4em;
    }
    .fox-user{
      margin-bottom: 20px;
      img{width: 60px;height: 60px; object-fit: cover;border-radius: 50%;padding: 5px; border: 2px solid  $red;}
      label{padding-left: 20px;font-weight: 500;color: #615F5F;}
    }
  }
}
.fox-tabbar{
  animation: project-tabbar 1.6s linear;
  .tabbar-header{
    ul{
      position: relative; width: 100%;
      li{
        position: relative;cursor: pointer;
        .btn-inside{
            padding: 15px; font-weight:500;font-size: 1.2em;
        }
        .btn-inside::after{
          content: '';position: absolute; bottom: 0;left: 0; height: 2px; background-color: $red; z-index: 1;width: 100%; opacity: 0;
        }
        .active::after{
          opacity: .7;
        }
      }
      li:hover .btn-inside::after{
        opacity: 1;
      }
    }
    ul::before{
      content: '';position: absolute; bottom: 0;left: 0; height: 2px; background-color: #ccc; z-index: 1;width: 100%; right: 0;
    }
  }
  .tabbar-scenes{
    .tabbar-scenes-item{
      min-height: 50vh; margin-top: 30px;
      overflow: hidden;
      img{
        width: 100%; margin-top: 15px;
      }
      p{}
    }
  }
}
.more-project{
  padding-left: 20px;
  .more-header{
    position: relative; width: 100%;
    .header-text{
      padding: 15px; font-weight:500;padding-left: 0; font-size: 1.2em;
    }
  }
  .more-header::after{
    content: '';position: absolute; bottom: 0;left: 0; height: 2px; background-color: #ccc; z-index: 1;width: 100%; right: 0;
  }
  .more-content{
    margin-top:30px;
    .more-item{width: 100%;}
  }
}
.fox-category{
  label{font-weight: 500;}
  a{padding: 5px; border: 2px solid $primary-turqoise; border-radius: 4px;margin-left: 15px;}
}
.fox-invest{
    animation: project-more 1.2s linear;
}


@media(max-width:768px){
  .fox-detile{padding-right: 20px !important;}
  .more-project{padding-right: 20px !important;}
  .more-item{width: 50% !important;}
}
@media(max-width:480px){
  .fox-detile{padding: 0 10px !important;}
  .more-project{padding: 0 10px !important;}
  .more-item{width: 100% !important;}
}

@keyframes project-banner{
  0%{transform: translateX(100%);opacity: 0}
  // 90%{transform: translateX(100%)}
  100%{transform: translateX(0%);opacity: 1}
}
@keyframes project-description{
  0%{transform: translateX(100%);opacity: 0}
  50%{transform: translateX(100%);opacity: 0}
  100%{transform: translateX(0%);opacity: 1}
}
@keyframes project-more{
  0%{transform: translateX(100%);opacity: 0}
  67%{transform: translateX(100%);opacity: 0}
  100%{transform: translateX(0%);opacity: 1}
}
@keyframes project-tabbar{
  0%{transform: translateX(100%);opacity: 0}
  75%{transform: translateX(100%);opacity: 0}
  100%{transform: translateX(0%);opacity: 1}
}
