.banner{
  .banner-image{
    width: 40%;
    padding: 15px;
    max-width: 400px;
    margin: auto;
    object-fit: contain;
    @media(max-width: 991px){
      width: 100%;
    }
    img{
      width: 100%;
    }
  }
  .banner-text{
    width: 60%;
    padding: 20px;
    .title{
      margin-bottom: 25px;
      .title-top{
        color: $blue;
        font-weight: 600;
        margin-bottom: 5px;
      }
      .title-bottom{
        font-size: 2em; color: $red;font-weight: 600;
      }
    }
    .description{
      font-weight: 500;
      p{
        margin-bottom: 8px; text-align: justify;
      }
    }
    @media(max-width: 991px){
      width: 100%;
    }
  }
}
