.btn-left{
  .btn-icon{width: 22px; margin-right: 10px;}
  *{cursor: pointer;}
  text-decoration: none !important;
  label{margin-top: 6px;    overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
}
@media(max-width:768px){
  .main-left-bar{
      order: 2
  }
  .main-right-bar{
    order: 1;
  }
}
