.fox-option-bar{
  padding: 15px 10px;
  .fox-option-bar-btn{
    padding: 5px;
    .boxed{
      background-color: #9a9a9a;
      color: #fff;
      border-radius: 50%;
      font-size: 1.4em;
      box-shadow: 0 0 5px rgba(3,3,3,.5);
      width: 60px; height: 60px;cursor: pointer;
      .icons{
        height: 45px;
      }
    }
    .boxed:not(.disabled):hover{background-color: #c23539 !important;}
    .active{
      background-color: #c23539 !important;
    }
  }
}
