.fox-footer{
  padding: 20px 10px;
  .fox-footer-top{
    padding: 60px 0px;
    .foot-img{
      padding: 0 30px 0 0;
      text-align: center;
      @media (max-width:480px){
        padding: 0 0px 0 0;
      }
      img{width: 80%;object-fit: contain;padding: 5px;}
      .foot-img-text{
        color: #fff;line-height: 3em; text-align: center; text-transform: uppercase;margin-top: -20px;
      }
    }
    ul{
      li{
        margin-bottom: 15px;
        a{color: #fff;}
      }
    }
    .foot-title{
      font-size: 1.3em; color: #fff;font-weight: 500; padding: 0 0 20px 0;
    }
  }
  .fox-footer-bottom{
    ul{
      li{
        a{color: #fff;} padding-right: 20px;
      }
    }
    .copy{
      color: #fff;
    }
  }
}
