.fox-search-box{
  max-width: 768px; width: 100%; position: relative; border-radius: 5px; overflow: hidden; border: none;
  input{
    width: 100%; padding: 15px 20px; border: none;font-size: 1.2em;
  }
  .icon{
    position: absolute; right: 0;top: 0;height: 100%; width: 50px;padding: 10px;color: $gray;
  }
}

.input-parent{
  .listing-member{
    margin-bottom: -10px;
    .listing-member-item{
      padding: 10px 0;
      border-bottom: 1px solid #c1c1c1; position: relative;
      .img-user{
        width: 50px;height: 50px;border: 1px solid #c1c1c1;border-radius: 50%;position: relative;overflow: hidden;margin-right: 15px;
        img{width: 100%;height: 100%;position: absolute;left: 0;top: 0;object-fit: cover;}
      }
      .text{
        font-weight: 600;opacity: 0.7;
      }
      .btn-close{
        position: absolute; right: 0; height: 20px;opacity: 0.6;cursor: pointer;
        &:hover{opacity:0.9}
      }
    }
    margin-bottom: 20px;
  }
  .text-title{
    z-index: 18;
    position: relative;
    left: 8px;
    background-color: #fff;
    display: inline-block;
    top: 7px;
    font-weight: 600;
    color: #565656;
    padding: 2px 2px;
    font-size: 13px;
  }
  .text-title-row{
    font-weight: 600;
    color: #565656;
    padding: 2px 2px;
    font-size: 13px;
    margin-bottom: 19px;
    line-height: 1.4em;
  }
  .fox-input,.fox-input-nospace{
    position: relative;
    border-radius: 2px;
    margin-bottom: 20px;
    padding-left: 0 !important;padding-right: 0 !important;
    .error{
      color: $red !important;    margin: 10px 0 20px 0;display: block;line-height: 1.3em;
    }
    .text-info{line-height: 1.4em;}
    input,textarea,.input-deactive,.input-img{
      border-radius: 4px;
      background-color: $white;
      box-shadow: 0 0 3px rgba(0,0,0,0.3);
      border: none;
      font-size: 16px;
      font-weight: 500;
      width: 100%;
      color: $gray;
      outline:none;
      left: 0;top:0;
      padding-left: 45px;
      padding-right: 18px;
      min-height: 48px;
    }
    textarea{
      padding-top: 15px;resize: vertical;
    }

    .select-dropdown{
      position: absolute;
      background-color: #fff;
      left: 0;
      margin-top: 10px;
      right: 0;
      z-index: 78;
      box-shadow: 0 0 8px rgba(3,3,3,0.5);-webkit-box-shadow: 0 0 5px rgba(3,3,3,0.5);-moz-box-shadow: 0 0 5px rgba(3,3,3,0.5);-o-box-shadow: 0 0 5px rgba(3,3,3,0.5);
      border-radius: 5px;
      overflow: hidden;overflow-y: auto;
      max-height: 300px;
      .select-item{
        padding: 15px;
        border-bottom: 1px solid #ccc;
        color: #565656; cursor: pointer;
      }
      .select-item:hover{
        background-color: #a53136;
        color: #fff; opacity: 0.8;
      }
      .active{
        background-color: #a53136;
        color: #fff; opacity: 0.8;
      }
    }
    .input-deactive{padding-top: 15px; box-shadow: none; border-bottom: 1px Solid #ccc;border-top: 1px Solid #ccc; border-radius: 0;overflow: hidden; text-overflow: ellipsis; white-space: nowrap;}
    .input-img{padding-top: 15px; box-shadow: none; border-bottom: 1px Solid #ccc;border-top: 1px Solid #ccc; border-radius: 0;overflow: hidden;}
    .mandatory{position: relative;}
    .mandatory::after{    position: absolute;
      right: -15px;
      color: red;
      content: '*';
      font-size: 22px;
      top: -8px;
    }
    .input-icon{
      position: absolute;
      left: 0;top: 11px;z-index: 2;
      padding: 3px 15px;
      height: 24px;
    }
    .input-icon-eye{
      position: absolute;
      right: 0;top: 11px;z-index: 2;
      padding: 3px 10px;
      height: 24px;
    }
    .drop-icon{
      position: absolute;
      color: #565656;cursor: pointer;
      right: 0;top: 11px;z-index: 2;
      padding: 5px; background-color: #f5f5f5; top: 0;
      height: 48px;
    }

  }
  .fox-input-nospace{
    .input-text{padding-left: 18px;padding-right: 18px;}
  }
}

.fox-form{
  padding: 10px 8%;
  @media(max-width: 350px){
    padding: 10px 10px;
  }
}
