.modal-window{
  position: fixed; top: 0;left: 0; width: 100%;height: 100%;z-index: 999999; background-color: rgba(3,3,3,0.2); text-align: center; max-height: 100vh;
  .modal-box{
    position: relative;padding: 15px;
    width: 100%;display: inline-grid;height: 100%;overflow: hidden;overflow-y: auto;
    .modal-content{
      animation: animFadeIn .4s linear;
      min-width: 320px; text-align: left; max-width:768px;
      position: relative; background-color: #fff; display:inline-block; margin-left: auto; margin-right: auto; padding: 30px 15px; border-radius: 5px;padding-top: 15px;vertical-align: middle;margin-top: auto;margin-bottom: auto;
      .modal-btn-close{
        position: absolute; right: 10px; top: 10px; cursor: pointer;color: $gray; height: 15px;
      }
      .modal-btn-close:hover{
        color: $red;
      }
    }
    .modal-hide{
      animation: animFadeOut .5s linear !important;
    }
  }
}

.modal-titles{font-size: 18px;font-weight: 400;margin: 15px 0;display: block; line-height: 1.4em;}
.line-box{
  width: 100%;position: relative;
  display: flex;display: -webkit-flex;
  flex: 1;-webkit-flex: 1;-moz-flex: 1;-o-flex: 1;margin: 15px 0;
  justify-content: center;-webkit-justify-content: center;-moz-justify-content: center;-o-justify-content: center;
  align-items: center;-webkit-align-items: center;-moz-align-items: center;-o-align-items: center;
  label{
    padding: 0 15px;margin-bottom: 0;    position: relative;z-index: 10; background-color: $white;
  }
}
.line-box::after{
  content: ''; width: 100%;position: absolute;left: 0;height: 1px;background-color: #adadad;
}
.login-page{
  padding: 50px 0; margin-top: 110px; margin-bottom: 50px;background-color: #f1f1f1;
}

@keyframes animFadeIn{
  0%{transform: scale(0.7);}
  50%{transform: scale(1.2);}
  100%{transform: scale(1);}
}
@keyframes animFadeOut{
  0%{transform: scale(1);}
  50%{transform: scale(1.2);}
  100%{transform: scale(0.5);}
}
