.fox-flat-list{
  .list-item,.list-item-column{
    padding: 15px; position: relative;text-decoration: none;
    animation: list-item .3s linear;
    *{cursor: pointer;}
    .list-item-box{
      background-color: #fff; padding-bottom: 15px;border-radius: 7px;overflow: hidden;
      height: 100%; box-shadow: 0 0 5px rgba(3,3,3,0.4);-webkit-box-shadow: 0 0 5px rgba(3,3,3,0.4);-moz-box-shadow: 0 0 5px rgba(3,3,3,0.4);-o-box-shadow: 0 0 5px rgba(3,3,3,0.4);
      .list-item-img{
        position: relative;
        padding-top: 70%;overflow: hidden;
        margin-bottom: 15px;
        img{width: 100%;position: absolute;top: 0;left: 0;height: 100%;object-fit: cover;}
      }

      .list-item-content{
        height: 100%;
        padding: 0 15px;
        .title,.price{
          font-weight: 500;text-align: left; color: #615F5F;font-size: 1.2em; margin-bottom: 15px;
        }
        .price-box{
          padding-top: 5px; border-top: 2px solid #ccc; margin-top: 10px;
          .price{
            font-size: 20px; color: $red; margin-top: 0 !important;font-weight: 500;
          }
        }
        .sub-title{
          text-align: left; color: #615F5F;    font-size: 1em;
          word-break: break-all;
          display: block; /* or inline-block */
          text-overflow: ellipsis;
          word-wrap: break-word;
          overflow: hidden;
          max-height: 6.5em;
          line-height: 1.3em;
        }
      }
    }
  }
  .list-item:hover,.list-item-column:hover{
    .list-item-box{box-shadow: 0 0 10px rgba(3,3,3,0.5);}
  }
  .list-item-column{
    .list-item-content{height: auto !important;margin-bottom: 10px;}
    .list-item-img{padding-top: 50% !important}
  }
}


.list-team-item{
  position: relative;min-height: 100px;padding-left: 110px;width: 100%;margin-bottom:15px;
  border: 1px solid #ccc;
  border-radius: 10px;
  @media(max-width: 480px){
    padding-left: 65px;
  }
  .img-box{
    width: 100px; height: 100px; object-fit: cover; position: absolute;left: 0; top: 0;border-radius: 5px;border: 2px solid #c5c5c5;
    @media(max-width: 480px){
      width: 60px;height: 60px; top: 0; bottom: 0;margin: auto;left: 5px;
    }
  }
  .text{
    .title{
      font-size: 1.3em;
    }
    .icons{width: 18px;margin-right: 8px; color: $red;}
    width: 100%;min-height: 100px;padding: 10px;
    //background-color: #fbfbfb;
    //border-radius: 9px;
    span{
      min-width: 100px;
    }
    .btn{padding: 0px;
      @media(max-width: 480px){
        width: 100%;
      }
    }
    .title-small{
      margin-bottom: 8px; margin-right: 20px;
    }
  }
}
@keyframes list-item{
  0%{transform: scale(0.7);}
  100%{transform: scale(1);}
}
