.fox-editor{
  border: 2px solid #e0e0e0;margin: 15px 0;
  border-radius: 5px;
  resize: vertical;
  .toolbar{
    padding: 10px;background-color: #e0e0e0;position: relative;top: 0;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0,0,0,.4);-webkit-box-shadow: 0 0 5px rgba(0,0,0,.4);-moz-box-shadow: 0 0 5px rgba(0,0,0,.4);-o-box-shadow: 0 0 5px rgba(0,0,0,.4);
    .btn{
      padding: 5px;cursor: pointer;
    }
    .btn:hover{
      color: $red;
    }
  }
  .context-menu{
    max-height: 300px;overflow: hidden;overflow-y: auto;
    .btn{
      padding: 5px;
      border-bottom: 1px solid #e4e4e4;
      margin-bottom: 10px;
      .main-icon {
          height: 16px;
      }
      i{
        margin-left: 10px;margin-right: 15px;
        text-transform: none;
        font-weight: 500;cursor: pointer;
      }
    }
  }
  .editor{
    outline: none;
    padding: 10px; min-height: 300px;
    line-height: 1.4em;
    img{width:100%;margin: 10px 0}
  }
}
