.map-box{
  width: 100%;
  .mapper{
    width: 66.6666%; padding: 10px;
    .map{
      position: relative; padding-top: 66%; overflow: hidden;
      iframe{
        position: absolute;left: 0;top: 0; height: 100%; width: 100%; background-color: #ccc;
      }
    }
  }

  .contact-info{
    width: 33.3333%; padding: 10px; height: 100%;
    .title{
      font-size: 1.2em;
      font-weight: 500;
      margin-bottom: 8px;
      color: $red;
    }
    .description{
      margin-bottom: 15px;
    }
  }

}

.kontak-info{
  .sort-story{
    margin-bottom: 10px; color: $red;
  }
  .item-text{
    padding: 0px 0; margin-bottom: 15px;
    label{
      position: absolute; left: 0; top: 20px; font-size: 1.6em;
    }
  }
}
.legalitas-list{
  .main-title{
    z-index: 1;
    position: relative;
    font-size: 2em;
    text-align: center;
    color: #041D57 !important;
    margin-bottom: 15px;
    margin-top: 20px;
    margin-bottom: 30px;
  }
  .items{
    margin-bottom: 25px;
    padding: 20px 25px;
    .title{
      display: inline-block;
      padding: 5px 15px;
      border-radius: 10px;
      border: 2px solid #a53236;
      color: #a53236;
    }
    .content-value{
      opacity: 0.7;
    }
  }
}

.text-right{
  text-align: right;
}
.text-left{
  text-align: right;
}
.text-center{
  text-align: right;
}
