.fox-image{
  position: relative;padding-top: 100%;overflow: hidden;border-radius: 5px;height: 0;box-shadow: 0 0 4px rgba(3,3,3,.3);-webkit-box-shadow: 0 0 4px rgba(3,3,3,.3);-moz-box-shadow: 0 0 4px rgba(3,3,3,.3);
  img{
    position: absolute; top: 0;left: 0;width: 100%;height: 100%;
  }
  .cover{object-fit: cover;-webkit-object-fit: cover;-moz-object-fit: cover;-o-object-fit: cover;}
  .contain{object-fit: contain;-webkit-object-fit: contain;-moz-object-fit: contain;-o-object-fit: contain;}
  .btn{height: 50px;width: 50px;
     position: absolute;z-index: 10;cursor: pointer;padding: 10px;background-color: $primary-turqoise; border-radius: 10px;color: $white;left: 0;right: 0;top: 0;bottom: 0;margin: auto;
    .icon{
      transform: rotate(180deg);-webkit-transform: rotate(180deg);-moz-transform: rotate(180deg);-o-transform: rotate(180deg);
      height: 100%;
    }
    input{width: 0;height: 0;display: none;}
  }
  .btn:hover{
    color: $white;
    background-color: $red;
  }
}
