.button-boxes{
  padding: 10px;
  *:not(.disabled){cursor: pointer;}
  .fox-button{
    background-color: $primary-turqoise; font-weight: 500; cursor: pointer;
    color: $white;
    font-size: 1em;
    padding: 10px 25px;
    text-align: center;
    border-radius: 4px;
    a{text-decoration: none;color: default;}
  }
  .btn-red-rounded{border:2px solid $red ;border-radius: 50px; color: $red;background-color: $white;}
  .btn-red-rounded-big{padding: 20px 25px;border:2px solid $red;font-size: 1.3em;color: $red;background-color: $white;border-radius: 50px;}
  .btn-red-rounded:hover,.btn-red-rounded-big:hover{
    background-color: $red !important;color: #fff;
    *{color: #fff !important;}
  }
  .btn-red{
    background-color: $red !important;color: #fff;
    *{color: #fff !important;}
  }
  .btn-wided{min-width: 200px}

  .btn-primary{
    min-height: 48px;
    display: flex;display: -webkit-flex;
    justify-content: center;-webkit-justify-content: center;-moz-justify-content: center;-o-justify-content: center;
    align-items: center;-webkit-align-items: center;-moz-align-items: center;-o-align-items: center;
  }
  .btn-primary:hover,.btn-red:hover{
    opacity: 0.5 ;
  }
  .btn-google{
    background-color: #dc5a5f;
    label{padding-left: 10px;}
  }
  .input-icon-medsos{
    width: 22px;
  }
}
