
body {
    font-family: "Poppins", sans-serif;
    font-size: 15px;
    background: $white-color;
    overflow-x: hidden;
    margin: 0;
    padding: 0;
    background-color: #f1f1f1;
}

hr { border: none; height: 1px; color: $gray-7; background-color: $gray-4;
    &.hr-footer{
        background-color: rgba(247,249,250,0.1);
        height: 1px;
        margin-top: 2rem;
        margin-bottom: 2.5rem
    }
}
*{
  line-height: 25px;
}
.root{
  background-color: #f1f1f1;
}

.fox-container{
  width: 100%;
  max-width: 1160px;
  margin-left: auto;margin-right: auto;
  padding-left: 15px;padding-right: 15px;
  overflow: hidden;position: relative;
}
@media (min-width: 576px) {
  .fox-container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .fox-container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .fox-container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .fox-container {
    max-width: 1140px;
  }
}
.fox-block{display: block;}
.fox-flex,.fox-flex-m,.fox-flex-s{display: flex;display: -webkit-flex;}
.fox-row,.fox-row-m,.fox-row-s{flex-direction: row;-webkit-flex-direction: row;-moz-flex-direction: row;-o-flex-direction: row;-ms-flex-direction: row;}
.fox-column,.fox-column-m,.fox-column-s{flex-direction: column;-webkit-flex-direction: column;-moz-flex-direction: column;-o-flex-direction: column;-ms-flex-direction: column;}
.fox-just-center,.fox-just-center-m,.fox-just-center-s{justify-content: center;-webkit-justify-content: center;-moz-justify-content: center;-o-justify-content: center;}
.fox-just-start,.fox-just-start-m,.fox-just-start-s{justify-content: flex-start;-webkit-justify-content: flex-start;-moz-justify-content: flex-start;-o-justify-content: flex-start;}
.fox-just-space,.fox-just-space-m,.fox-just-space-s{justify-content: space-between;-moz-justify-content: space-between;-webkit-justify-content: space-between;-o-justify-content: space-between;}
.fox-just-around,.fox-just-around-m,.fox-just-around-s{justify-content: space-around;-webkit-justify-content: space-around;-moz-justify-content: space-around;-o-justify-content: space-around;}
.fox-just-end,.fox-just-end-m,.fox-just-end-s{justify-content: flex-end;-webkit-justify-content: flex-end;-moz-justify-content: flex-end;-o-justify-content: flex-end;}
.fox-align-center,.fox-align-center-m,.fox-align-center-s{align-items: center;-webkit-align-items: center;-moz-align-items: center;-o-align-items: center;}
.fox-align-start,.fox-align-start-m,.fox-align-start-s{align-items: flex-start;-webkit-align-items: flex-start;-moz-align-items: flex-start;-o-align-items: flex-start;}
.fox-align-end,.fox-align-end-m,.fox-align-end-s{align-items: flex-end;-webkit-align-items: flex-end;-moz-align-items: flex-end;-o-align-items: flex-end;}
.fox-align-strench,.fox-align-strench-m,.fox-align-strench-s{align-items: stretch;-webkit-align-items: stretch;-moz-align-items: stretch;-o-align-items: stretch;}
.fox-wrap{flex-wrap: wrap;-webkit-flex-wrap: wrap;-moz-flex-wrap: wrap;-o-flex-wrap: wrap;}

.fox-mr-10{margin: 10px;}
.fox-mb-10{margin-bottom: 10px;}
.fox-mt-10{margin-top: 10px;}
.fox-p-10{padding: 10px;}
.fox-pt-10{padding-top: 10px;}
.fox-mb-15{margin-bottom: 15px;}

.bg-red{background-color: $red}
.bg-white{background-color: #fff}
.bg-tail{background-color: $tail}
.bg-red-dark{background-color: #942d2d}
.bg-gray-soft{background-color:#f9f9f9 }

.c-red{color:  $red;}
.c-gray{color: $gray-2;}
.main-icon{height: 24px;}


.fox-v-100{height: 100%;}
.fox-h-100{width: 100%;}

.fox-box-shadow-4{box-shadow: 0 0 4px rgba(3, 3, 3, 0.2);}

.fox-center{text-align: center;}
.fox-fixed{position: fixed;left: 0;}
.fox-margin-fixed{height: 0px;}
.fox-anim,.fox-anim *{transition: all .3s linear}


.fox-col-1,.fox-col-1-m,.fox-col-1-s{width: 100% ;}
.fox-col-66,.fox-col-66-m,.fox-col-66-s{width: 66.66666%;}
.fox-col-2,.fox-col-2-m,.fox-col-2-s{width: 49.99999%;}
.fox-col-3,.fox-col-3-m,.fox-col-3-s,.fox-col-33,.fox-col-33-m,.fox-col-33-s{width: 33.33333%;}
.fox-col-4,.fox-col-4-m,.fox-col-4-s{width: 24.99999%;}
.fox-col-5,.fox-col-5-m,.fox-col-5-s{width: 19.99999%;}
.fox-col-6,.fox-col-6-m,.fox-col-6-s{width: 16.66666%;}
.fox-col-7,.fox-col-7-m,.fox-col-7-s{width: 14.28444%;}
.fox-col-8,.fox-col-8-m,.fox-col-8-s{width: 12.5%;}
.fox-col-9,.fox-col-9-m,.fox-col-9-s{width: 11.11111%;}
.fox-col-10,.fox-col-10-m,.fox-col-10-s{width: 10%;}

.fox-background{
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background 0.3s, border 0.3s, border-radius 0.3s, box-shadow 0.3s;
}
.no-pad{padding: 0 0 !important;}
.pl-0{padding-left: 0 !important;}
.pl-25{padding-left: 25px !important;}
.fox-text-left{text-align: left !important;}
.mb-15{margin-bottom: 15px;}

.max-width-480{max-width: 480px !important; margin-left: auto; margin-right: auto;}
.max-width-450{max-width: 450px !important; margin-left: auto; margin-right: auto;}
.max-width-320{max-width: 320px !important;margin-left: auto; margin-right: auto;}
.own-icon{height: 25px;}
.img-uploud-icon{
  height: 25px; position: absolute;z-index: 10;
}
.hide-input{width: 0;height: 0;display: none;}
.bt-1{border-top: 1px Solid #ccc;}
.mb-2{margin-bottom: 2px !important;}


@media (max-width:523px){
  .fox-column-m{flex-direction: column;-webkit-flex-direction: column;-moz-flex-direction: column;-o-flex-direction: column;-ms-flex-direction: column;}
  .fox-col-1-m,.fox-col-66-m,.fox-col-33-m,.fox-col-2-m,.fox-col-4-m,.fox-col-3-m,.fox-col-6-m,.fox-col-8-m,.fox-col-5-m,.fox-col-7-m,.fox-col-5-m,.fox-col-7-m,.fox-col-9-m,.fox-col-10-m{width: 100% !important;}
  .fox-just-space-m{justify-content: center;-webkit-justify-content: center;-moz-justify-content: center;-o-justify-content: center;}
}
@media (max-width:1024px){
  .fox-col-1-m{width: 100% !important;}
  .fox-col-66-m{width: 66.66666% !important;}
  .fox-col-2-m,.fox-col-4-m{width: 49.99999% !important;}
  .fox-col-3-m,.fox-col-6-m,.fox-col-33-m{width: 33.33333% !important;}
  .fox-col-8-m{width: 24.99999% !important;}
  .fox-col-5-m,.fox-col-7-m{width: 19.99999% !important;}
  .fox-col-9-m,.fox-col-10-m{width: 16.66666% !important;}
}
@media (max-width:768px){
  .show{display: block !important;}
  .fox-row-m{flex-direction: column;}
  .fox-column-m{flex-direction: row;-webkit-flex-direction: row;-moz-flex-direction: row;-o-flex-direction: row;-ms-flex-direction: row;}
  .fox-col-1-m,.fox-col-66-m,.fox-col-33-m,.fox-col-2-m{width: 100% !important;}
  .fox-col-3-m,.fox-col-4-m{width: 49.99999% !important;}
  .fox-col-3-m:not(.item),.fox-col-6-m{width: 33.33333% !important;}
  .fox-col-8-m{width: 24.99999% !important;}
  .fox-col-5-m,.fox-col-7-m{width: 19.99999% !important;}
  .fox-col-9-m,.fox-col-10-m{width: 16.66666% !important;}
}

.rounded-box{
  background-color: #fff; border-radius: 10px; margin-bottom: 25px; padding: 15px;
}
.no-pad{
  padding: 0 !important;
}
.no-margin{ margin-bottom: 0 !important; margin: 0;}
.blog{
  padding-left: 5%; padding-right: 5%;
  *{
    line-height: 2em;
  }
  p,img,ul{
    padding: 0;
    margin-bottom: 20px;
  }
}
.tab-space{
  width: 30px;display: inline-block;
}
