.coming-soon{
  .coming-soon-list{
    overflow: hidden;padding: 10px; width: 100%;
    @media(max-width: 680px){
      padding: 0 !important;
    }
    .coming-soon-item{
      width: 100%; position: relative; padding-top: 70%; overflow: hidden; border-radius: 10px;
      img{
        width: 100%; position: absolute;left: 0;height: 100%; top: 0; object-fit: cover;
      }
    }
  }
}
.part-title{
  padding: 10px 0;
  margin-top: 15px;
  font-size: 21px;
  font-weight: 500;
  //color: #777;
  z-index: 1;
  position: relative;
  color: #041D57 !important;
  //margin-bottom: 15px;
  //margin-top: 20px;
}
