.fox-content{
  padding: 30px 0;position: relative;
  .content-title{
    z-index: 1;
    position: relative;
    font-size: 2em;
    text-align: center;
    color: #041D57 !important;
    margin-bottom: 15px;
    margin-top: 20px;
  }
  .content-sub-title{
    font-size: 1.14286em;
    text-align: center;
    color: #615F5F;
    margin-bottom: 15px;
    z-index: 1;
    position: relative;
  }
  .content-value{
    padding: 15px 0;
    z-index: 1;
    position: relative;
  }
}

.who-we-are,.kontak-info{
  padding-bottom: 15px; overflow: hidden;padding-top: 15px; padding-left: 5%; padding-right: 5%;
  .img{
    text-align: center;
    img{
      width: 70%; padding: 5px; border-radius: 80px;
    }
  }
  .sort-story{
    font-size: 1.14286em;
    color: #615F5F;
    font-weight: 500;
    margin-bottom: 15px;
  }
  .item-text{
    padding: 20px 0;
    font-size: 1.1em;
    position: relative;
    line-height: 1.5em;
    opacity: 0.8;
    font-weight: 500;
    label{
      position: absolute; left: 0; top: 20px; font-size: 1.6em;
    }
  }
}

.user-img-card{
  padding: 20px;
  .user-img-container,.user-img-container-indcrowd{
    border-radius: 50%;
    margin-bottom: 20px;
    padding: 20px;
    border: 4px solid #fff;
    .user-img{
      position: relative;
      border-radius: 50%;
      padding-top: 100%;overflow: hidden;
      img{width: 100%;position: absolute;top: 0;left: 0;height: 100%;}
    }
  }
  .user-img-title{
    text-align: center; color: #615F5F;    font-size: 1.5em; margin-bottom: 15px;
  }
  .user-img-text{
    text-align: center; color: #041D57;    font-size: 1.2em;
    line-height: 1.2em;
  }
  .user-img-container-indcrowd{
    border-radius: 5px;
    margin-bottom: 15px;
    padding: 10px;
    border: 3px solid #fff;
    .user-img{
      border-radius: 0;
      padding-top: 46%;
      img{object-fit: contain;}
    }
  }
}


