.fox-header{
  width: 100%;
  height: 80px;
  top: 0;left: 0;z-index: 1000;
  color: $red;
  a{text-decoration: none;}
  .fox-logo{
    padding: 5px 10px;overflow: hidden;height: 100%;
    img{
      height: 100%;
      object-fit: contain; -webkit-object-fit: contain;-moz-object-fit: contain;
    }
    .logo-text{
      text-align: left; padding: 5px; padding-left: 8px;
      .title{
        color: $red; font-weight: 600;
        font-size: 1.4em;
      }
      .slogan{
        color: #333;
        font-size: 12px;
        margin-top: -7px;
        letter-spacing: 7px;
        margin-right: -7px;
        font-weight: 500;
      }
    }
  }
  .fox-navigator{
    height: 100%;
    .fox-btn-box-m{
      @media(max-width: 768px){
        transform: translate(-100%);-moz-transform: translate(-100%);-webkit-transform: translate(-100%);-o-transform: translate(-100%);
      }
    }
    .fox-btn-box-show{
      display: none;
      @media(max-width: 768px){
        display: unset;
      }
    }
    ul{padding: 0; margin: 0;text-decoration: none;list-style: none; height: 100%;
      li{text-decoration: none;height: 100%;position: relative;
        border-bottom: 1px solid #ccc;
        .fox-nav-btn{padding: 10px;}
        .nav-icon{
            height: 40px;color: $gray;border: 2px solid $gray;padding: 5px;border-radius: 50%;margin-left: 20px;margin-right: -5px;
            @media(max-width: 768px){margin-left: 0;}
        }
        .nav-icon-menu{height: 40px;color: $gray;border: 2px solid $gray;padding: 5px;border-radius: 3px;}

        *{cursor: pointer;}
      }
      li::after{content: '';width: 100%;height: 3px;background-color: transparent;position: absolute;bottom: 0;left: 0;transition: .4s all;border-radius: 10px 10px 0 0}
      li::before{content: '';width: 100%;height: 3px;background-color: transparent;position: absolute;top: 0;left: 0;transition: .4s all;border-radius: 0 0 10px 10px;}

      li:hover{
        *{color: $red; border-color: $red;}
      }
      li:hover::after,li:hover::before{
        background-color: $red;height: 8px;
      }
      .active{*{color: $red;border-color: $red !important;}}
      .active::after,.active::before{background-color: $red;}
    }
    .nav-show{
      @media(max-width: 768px){
        display: flex !important;display: -webkit-flex !important;display: -moz-flex !important;
        transform: translate(0%);-webkit-transform: translate(0%);-moz--webkit-transform: translate(0%);-o--webkit-transform: translate(0%);
        justify-content: flex-start;-moz-justify-content: flex-start;-webkit-justify-content: flex-start;-o-justify-content: flex-start;
        flex-direction: column;-webkit-flex-direction: column;-moz-flex-direction: column;-o-flex-direction: column;
      }
    }
    //.nav-hide{animation:  animNavHide .2s linear;}
    .header-m{
      @media(max-width: 768px){
        position: fixed;
        left: 0;
        top: 80px;
        z-index: 9999;
        background-color: #efefef;
        width: 100%;
        li{
          text-decoration: none;
          position: relative;
          display: block;
          width: 100%;
          padding: 20px;
          height: auto;
        }
      }
    }
  }
}
.fox-head-margin{height: 0px !important;}
