.BrainhubCarousel__dots{
    align-items: center;-webkit-align-items: center;-moz-align-items: center;
    -webkit-justify-content: center;-moz-justify-content: center;
    margin-bottom: 30px;
    margin-top: -20px;
  .BrainhubCarousel__dot{
    line-height: 0;
    &:before{
      width: 15px;
      height: 15px;
    }
  }
  .BrainhubCarousel__dot--selected{
    &:before{
      width: 20px;
      height: 20px;
    }
  }
}
