.project-container{
  .left-bar{
    width: 20%;margin-bottom: 30px; padding-right: 15px;border-right: 2px solid #f1f1f1;position: relative;z-index: 20;background-color: $white;
    .project-header{
      padding: 20px 0px;
      .header-text{
        font-weight: 500; font-size: 1.2em; color: $gray-1;
      }
    }
    .project-content{
      ul{ display: block; margin-bottom: 30px;
        li{
          *{transition: none !important;cursor: pointer;line-height: 1.4em;}
          .category-text{margin-bottom: 5px;color: $gray; padding: 10px 0; border-bottom: 2px solid $red}
          a{color: $gray; padding: 10px;display: block;font-weight: 300;}
          a:hover{color: $red; font-weight: 500;}
          .active{color: $red; font-weight: 500;}
        }
      }
    }
  }
  .item-bar{
    width: 100%;margin-bottom: 30px;overflow: hidden;
    .project-header{
      padding: 20px 10px;
      .header-text{color: $gray-1;}
    }
    .project-content{

    }
  }
}
.show-height{
  margin-top: 0 !important;
}
.board{
  .item-bar{
    width: 80%;margin-bottom: 30px;padding-left: 20px;overflow: hidden;
    .project-header{
      padding: 20px 10px;
      .header-text{color: $gray-1;}
    }
    .project-content{

    }
  }
}

.fox-top-bar{
  // padding-left: 20%;
  // @media(max-width: 768px){
  //   padding-left: 15px;
  // }
  position: relative;
  .top-title-main{font-weight: 500; font-size: 1.2em; color: $gray-1;min-width: 20%;}
  .top-title{font-weight: 500; font-size: 1.2em; color: $gray; margin: 10px 0;padding: 15px 0;}
  .top-btn{
    position: absolute;color: $gray;cursor: pointer;left: 0; display: none;border: 2px solid $gray;padding: 5px; border-radius:3px ;
    &:hover{
      color: $red;
    }
  }
}

@media(max-width:768px){
  .top-btn{display: block !important; }
  .item-bar,.left-bar{width: 100% !important;padding-left: 0 !important;padding-right: 0 !important}
  .left-bar{ border-right:  none !important; border-bottom:  2px solid $red;}
  .left-bar:not(.main){margin-top: -200%;}
  .top-title-main{padding-left: 50px !important;margin-right: 10px;}
}
